<template>
  <div id="navbarContent" class="m-0 p-0 justify-content-end align-self-center">
    <div class="nav-item dropdown m-0 p-0">
      <router-link
        class="nav-item nav-link m-0 p-0"
        to="#"
        title="Mon compte"
        data-toggle="dropdown"
        id="my_account"
        aria-haspopup="true"
        aria-expanded="false"
      >
        <img
          :src="avatar"
          alt="Image de profile"
          title="Image de profile"
          height="37"
          class="my-0 rounded-circle"
        />
      </router-link>
      <div
        class="dropdown-menu dropdown-menu-right"
        aria-labelledby="my_account"
      >
        <p class="h5 dropdown-item-text">{{ userName }}</p>
        <div class="dropdown-divider"></div>
        <div class="d-flex">
          <img
            src="/images/edit.svg"
            fill="text-primary"
            alt="Mon compte"
            title="Mon compte"
            class="pl-4"
          /><router-link to="/compte" 
          title="Aller  mon compte"
          class="dropdown-item pl-2"
            >Mon compte</router-link
          >
        </div>
        <div class="d-flex" v-if="this.isAdmin == 'true'">
          <img
            src="/images/info.svg"
            fill="text-danger"
            alt="Ecrire un article"
            title="Ecrire un article"
            class="pl-4"
          /><router-link to="/compte/articles"  title="Voir les articles" class="dropdown-item pl-2"
            >Mes articles</router-link
          >
        </div>
         <div class="d-flex" v-if="this.isAdmin == 'true'">
           <img
            src="/images/info.svg"
            fill="text-danger"
            alt="Ecrire un article"
            title="Ecrire un article"
            class="pl-4"
          /><router-link to="/Users"  title="Voir les utilisateurs" class="dropdown-item pl-2"
            >Utilisateurs</router-link
          >
           </div>
        <div class="d-flex">
          <img
            src="/images/mail.svg"
            fill="text-primary"
            alt="Ecrire un message"
            title="Ecrire un message"
            class="pl-4"
          /><router-link to="/compte/messages"  title="Voir les messages" class="dropdown-item pl-2"
            >Mes messages</router-link
          >
        </div>
        <div class="dropdown-divider"></div>
        <div class="d-flex">
          <img
            src="/images/pass.svg"
            fill="text-primary"
            alt="se deconnecter"
            title="se deconnecter"
            class="pl-4"
          /><router-link
            class="dropdown-item pl-2 text-danger"
            title="se deconnecter"
            @click="deconnexion"
            to="/"
            >Déconnexion</router-link
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Swal from "sweetalert2";

export default {
  name: "NavUserMenu",
  data() {
    return {
      isAdmin: false,
      isActive: true,
      userName: "",
      avatar: "/images/avatars/default_user.jpg"
    };
  },
  created: function() {
    this.isAdmin = localStorage.getItem("role");
    this.userName = localStorage.getItem("userName");
    this.avatar = localStorage.getItem("avatar");
  },
  methods: {
    deconnexion: function() {
      localStorage.clear();
      Swal.fire({
        text: "Déconnexion en cours !",
        footer: "Redirection en cours...",
        icon: "info",
        timer: 2000,
        showConfirmButton: false,
        timerProgressBar: true,
        willClose: () => {
          location.reload();
        }
      });
    }
  }
};
</script>